.leaflet-container {
    width: 100%;
    height: 100vh;

    margin: 0px;
    padding: 0;
}
.scale {
    transition: 0.35s; /* Время эффекта */
}
.scale:hover {
    transform: scale(2); /* Увеличиваем масштаб */
}
.leaflet-popup-content-wrapper {
    width: 100%;
}


.leaflet-popup-content{
    width: 100%;
}
.popup{
    margin: 5px;
}

.popupInfoWrapperDiv {
    width: 100%;
}

.leaflet-container a.leaflet-popup-close-button{
    width: 36px;
    height: 28px;
    font: 27px/23px Tahoma, Verdana, sans-serif;
}